// Form control focus state
//
// Generate a customized focus state and for any input with the specified color,
// which defaults to the `@input-focus-border-color` variable.
//
// We highly encourage you to not customize the default value, but instead use
// this to tweak colors on an as-needed basis. This aesthetic change is based on
// WebKit's default styles, but applicable to a wider range of browsers. Its
// usability and accessibility should be taken into account with any change.
//
// Example usage: change the default blue border and shadow to white for better
// contrast against a dark gray background.
#form-control-focus() {
	&:focus {
		color: @input-focus-color;
		background-color: @input-focus-bg;
		border-color: @input-focus-border-color;
		outline: none;
		// Avoid using mixin so we can pass custom focus shadow properly
		& when (@enable-shadows) {
			box-shadow: @input-box-shadow, @input-btn-focus-box-shadow;
		}
		& when not (@enable-shadows) {
			box-shadow: @input-btn-focus-box-shadow;
		}
	}
}


#form-validation-state(@state, @color) {

	.@{state}-feedback {
		display: none;
		margin-top: 0.25rem;
		font-size: 0.875rem;
		color: @color;
	}

	.@{state}-tooltip {
		position: absolute;
		top: 100%;
		z-index: 5;
		display: none;
		width: 250px;
		padding: 0.5rem;
		margin-top: 0.1rem;
		font-size: 0.875rem;
		line-height: 1;
		color: #fff;
		background-color: fade(@color, 80%);
		border-radius: 0.2rem;
	}

	.form-control,
	.custom-select {
		.was-validated &:@{state},
		&.is-@{state} {
			border-color: @color;

			&:focus {
				box-shadow: 0 0 0 0.2rem fade(@color, 25%);
			}

			~ .@{state}-feedback,
			~ .@{state}-tooltip {
				display: block;
			}
		}
	}


	// TODO: redo check markup lol crap
	.form-check-input {
		.was-validated &:@{state},
		&.is-@{state} {
			+ .form-check-label {
				color: @color;
			}
		}
	}

	// custom radios and checks
	.custom-control-input {
		.was-validated &:@{state},
		&.is-@{state} {
			~ .custom-control-indicator {
				background-color: fade(@color, 25%);
			}
			~ .custom-control-description {
				color: @color;
			}
		}
	}

	// custom file
	.custom-file-input {
		.was-validated &:@{state},
		&.is-@{state} {
			~ .custom-file-control {
				border-color: @color;

				&::before { border-color: inherit; }
			}
			&:focus {
				box-shadow: 0 0 0 0.2rem fade(@color, 25%);
			}
		}
	}
}
